import type { 
  ApiResponse, 
  PaginatedResponse, 
  Course, 
  CourseFilters,
  CourseSortOptions,
  User,
  AuthCredentials,
  RegisterData,
  AuthResponse,
  CourseProgress,
  LearningStats,
  AIExerciseSubmission,
  QuizSubmission
} from '@/types';

// API Configuration
const API_BASE_URL = import.meta.env.VITE_API_BASE_URL || 'http://localhost:3000/api';
const API_VERSION = 'v1';

class ApiClient {
  private baseURL: string;
  private token: string | null = null;

  constructor() {
    this.baseURL = `${API_BASE_URL}/${API_VERSION}`;
    this.token = localStorage.getItem('auth_token');
  }

  // Set authentication token
  setToken(token: string | null) {
    this.token = token;
    if (token) {
      localStorage.setItem('auth_token', token);
    } else {
      localStorage.removeItem('auth_token');
    }
  }

  // Get authentication headers
  private getHeaders(): Headers {
    const headers = new Headers({
      'Content-Type': 'application/json',
    });

    if (this.token) {
      headers.set('Authorization', `Bearer ${this.token}`);
    }

    return headers;
  }

  // Generic request method
  private async request<T>(
    endpoint: string, 
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    const url = `${this.baseURL}${endpoint}`;
    const headers = this.getHeaders();

    try {
      const response = await fetch(url, {
        ...options,
        headers: {
          ...Object.fromEntries(headers),
          ...options.headers,
        },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error('API request failed:', error);
      throw error;
    }
  }

  // GET request
  async get<T>(endpoint: string, params?: Record<string, any>): Promise<ApiResponse<T>> {
    const url = new URL(`${this.baseURL}${endpoint}`);
    
    if (params) {
      Object.entries(params).forEach(([key, value]) => {
        if (value !== undefined && value !== null) {
          url.searchParams.append(key, String(value));
        }
      });
    }

    return this.request<T>(url.pathname + url.search);
  }

  // POST request
  async post<T>(endpoint: string, data?: any): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'POST',
      body: data ? JSON.stringify(data) : undefined,
    });
  }

  // PUT request
  async put<T>(endpoint: string, data?: any): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'PUT',
      body: data ? JSON.stringify(data) : undefined,
    });
  }

  // DELETE request
  async delete<T>(endpoint: string): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'DELETE',
    });
  }

  // PATCH request
  async patch<T>(endpoint: string, data?: any): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'PATCH',
      body: data ? JSON.stringify(data) : undefined,
    });
  }
}

// Create API client instance
const apiClient = new ApiClient();

// Authentication API
export const authApi = {
  async login(credentials: AuthCredentials): Promise<AuthResponse> {
    const response = await apiClient.post<AuthResponse>('/auth/login', credentials);
    if (response.success && response.data.token) {
      apiClient.setToken(response.data.token);
    }
    return response.data;
  },

  async register(userData: RegisterData): Promise<AuthResponse> {
    const response = await apiClient.post<AuthResponse>('/auth/register', userData);
    if (response.success && response.data.token) {
      apiClient.setToken(response.data.token);
    }
    return response.data;
  },

  async logout(): Promise<void> {
    await apiClient.post('/auth/logout');
    apiClient.setToken(null);
  },

  async refreshToken(refreshToken: string): Promise<AuthResponse> {
    const response = await apiClient.post<AuthResponse>('/auth/refresh', { refreshToken });
    if (response.success && response.data.token) {
      apiClient.setToken(response.data.token);
    }
    return response.data;
  },

  async getCurrentUser(): Promise<User> {
    const response = await apiClient.get<User>('/auth/me');
    return response.data;
  },

  async resetPassword(email: string): Promise<void> {
    await apiClient.post('/auth/reset-password', { email });
  },

  async verifyResetToken(token: string): Promise<boolean> {
    const response = await apiClient.post<{ valid: boolean }>('/auth/verify-reset-token', { token });
    return response.data.valid;
  },

  async updatePassword(token: string, newPassword: string): Promise<void> {
    await apiClient.post('/auth/update-password', { token, newPassword });
  }
};

// Courses API
export const coursesApi = {
  async getCourses(
    filters?: CourseFilters, 
    sort?: CourseSortOptions,
    page: number = 1,
    limit: number = 12
  ): Promise<PaginatedResponse<Course>> {
    const response = await apiClient.get<PaginatedResponse<Course>>('/courses', {
      ...filters,
      sortField: sort?.field,
      sortOrder: sort?.order,
      page,
      limit,
    });
    return response.data;
  },

  async getCourse(id: string): Promise<Course> {
    const response = await apiClient.get<Course>(`/courses/${id}`);
    return response.data;
  },

  async enrollInCourse(courseId: string): Promise<void> {
    await apiClient.post(`/courses/${courseId}/enroll`);
  },

  async getFeaturedCourses(): Promise<Course[]> {
    const response = await apiClient.get<Course[]>('/courses/featured');
    return response.data;
  },

  async getPopularCourses(): Promise<Course[]> {
    const response = await apiClient.get<Course[]>('/courses/popular');
    return response.data;
  },

  async searchCourses(query: string): Promise<Course[]> {
    const response = await apiClient.get<Course[]>('/courses/search', { q: query });
    return response.data;
  },

  async rateCourse(courseId: string, rating: number): Promise<void> {
    await apiClient.post(`/courses/${courseId}/rate`, { rating });
  }
};

// User Progress API
export const progressApi = {
  async getCourseProgress(courseId: string): Promise<CourseProgress> {
    const response = await apiClient.get<CourseProgress>(`/progress/courses/${courseId}`);
    return response.data;
  },

  async markLessonComplete(courseId: string, lessonId: string): Promise<void> {
    await apiClient.post(`/progress/courses/${courseId}/lessons/${lessonId}/complete`);
  },

  async markModuleComplete(courseId: string, moduleId: string): Promise<void> {
    await apiClient.post(`/progress/courses/${courseId}/modules/${moduleId}/complete`);
  },

  async getLearningStats(): Promise<LearningStats> {
    const response = await apiClient.get<LearningStats>('/progress/stats');
    return response.data;
  },

  async updateTimeSpent(courseId: string, timeSpent: number): Promise<void> {
    await apiClient.patch(`/progress/courses/${courseId}/time`, { timeSpent });
  }
};

// AI Exercises API
export const aiExercisesApi = {
  async submitExercise(exerciseId: string, answer: string): Promise<AIExerciseSubmission> {
    const response = await apiClient.post<AIExerciseSubmission>(
      `/ai-exercises/${exerciseId}/submit`, 
      { answer }
    );
    return response.data;
  },

  async getExerciseSubmissions(exerciseId: string): Promise<AIExerciseSubmission[]> {
    const response = await apiClient.get<AIExerciseSubmission[]>(
      `/ai-exercises/${exerciseId}/submissions`
    );
    return response.data;
  },

  async getHint(exerciseId: string): Promise<string> {
    const response = await apiClient.post<{ hint: string }>(`/ai-exercises/${exerciseId}/hint`);
    return response.data.hint;
  }
};

// Quiz API
export const quizApi = {
  async submitQuiz(quizId: string, answers: any[]): Promise<QuizSubmission> {
    const response = await apiClient.post<QuizSubmission>(`/quizzes/${quizId}/submit`, { answers });
    return response.data;
  },

  async getQuizResults(quizId: string): Promise<QuizSubmission[]> {
    const response = await apiClient.get<QuizSubmission[]>(`/quizzes/${quizId}/results`);
    return response.data;
  }
};

// User Management API
export const userApi = {
  async updateProfile(data: Partial<User>): Promise<User> {
    const response = await apiClient.patch<User>('/users/profile', data);
    return response.data;
  },

  async uploadAvatar(file: File): Promise<{ avatarUrl: string }> {
    const formData = new FormData();
    formData.append('avatar', file);
    
    const response = await fetch(`${apiClient['baseURL']}/users/avatar`, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${apiClient['token']}`,
      },
      body: formData,
    });

    if (!response.ok) {
      throw new Error('Failed to upload avatar');
    }

    const data = await response.json();
    return data.data;
  },

  async getEnrolledCourses(): Promise<Course[]> {
    const response = await apiClient.get<Course[]>('/users/enrolled-courses');
    return response.data;
  },

  async getCompletedCourses(): Promise<Course[]> {
    const response = await apiClient.get<Course[]>('/users/completed-courses');
    return response.data;
  }
};

export default apiClient;
