import { ref, computed } from 'vue';
import { defineStore } from 'pinia';
import { authApi } from '@/services/api';
import { mockUser, mockApiDelay } from '@/services/mockData';
import type { User, AuthCredentials, RegisterData, AuthResponse } from '@/types';

export const useAuthStore = defineStore('auth', () => {
  // State
  const user = ref<User | null>(null);
  const isLoading = ref(false);
  const error = ref<string | null>(null);

  // Getters
  const isAuthenticated = computed(() => !!user.value);
  const isInstructor = computed(() => user.value?.role === 'instructor');
  const isAdmin = computed(() => user.value?.role === 'admin');

  // Actions
  const login = async (credentials: AuthCredentials) => {
    isLoading.value = true;
    error.value = null;

    try {
      // Mock API call with delay
      await mockApiDelay(800);
      
      // Simulate successful login with any credentials
      const mockResponse: AuthResponse = {
        user: mockUser,
        token: 'mock-jwt-token-' + Date.now(),
        refreshToken: 'mock-refresh-token-' + Date.now()
      };
      
      user.value = mockResponse.user;
      localStorage.setItem('auth_token', mockResponse.token);
      
      return mockResponse;
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка входа';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const register = async (userData: RegisterData) => {
    isLoading.value = true;
    error.value = null;

    try {
      // Mock API call with delay
      await mockApiDelay(1000);
      
      // Create mock user from registration data
      const newUser: User = {
        ...mockUser,
        fullName: userData.fullName,
        email: userData.email,
        id: 'user-' + Date.now()
      };
      
      const mockResponse: AuthResponse = {
        user: newUser,
        token: 'mock-jwt-token-' + Date.now(),
        refreshToken: 'mock-refresh-token-' + Date.now()
      };
      
      user.value = mockResponse.user;
      localStorage.setItem('auth_token', mockResponse.token);
      
      return mockResponse;
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка регистрации';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const logout = async () => {
    isLoading.value = true;
    
    try {
      await authApi.logout();
    } catch (err) {
      console.error('Logout error:', err);
    } finally {
      user.value = null;
      isLoading.value = false;
    }
  };

  const getCurrentUser = async () => {
    const token = localStorage.getItem('auth_token');
    if (!token) return;
    
    isLoading.value = true;
    
    try {
      // Mock API call with delay
      await mockApiDelay(300);
      
      // If we have a token, set the mock user
      user.value = mockUser;
    } catch (err) {
      console.error('Get current user error:', err);
      // If token is invalid, logout
      user.value = null;
      localStorage.removeItem('auth_token');
    } finally {
      isLoading.value = false;
    }
  };

  const updateProfile = async (data: Partial<User>) => {
    if (!user.value) return;

    isLoading.value = true;
    error.value = null;

    try {
      const { userApi } = await import('@/services/api');
      const updatedUser = await userApi.updateProfile(data);
      user.value = updatedUser;
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка обновления профиля';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const resetPassword = async (email: string) => {
    isLoading.value = true;
    error.value = null;

    try {
      await authApi.resetPassword(email);
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка сброса пароля';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const clearError = () => {
    error.value = null;
  };

  // Initialize user on store creation
  const initializeAuth = async () => {
    const token = localStorage.getItem('auth_token');
    if (token) {
      await getCurrentUser();
    }
  };

  return {
    // State
    user,
    isLoading,
    error,
    
    // Getters
    isAuthenticated,
    isInstructor,
    isAdmin,
    
    // Actions
    login,
    register,
    logout,
    getCurrentUser,
    updateProfile,
    resetPassword,
    clearError,
    initializeAuth,
  };
});
