import { ref, computed } from 'vue';
import { defineStore } from 'pinia';
import { coursesApi, progressApi } from '@/services/api';
import { mockCourses, mockCourseProgress, mockApiDelay } from '@/services/mockData';
import type { 
  Course, 
  CourseFilters, 
  CourseSortOptions, 
  CourseProgress,
  PaginatedResponse 
} from '@/types';

export const useCoursesStore = defineStore('courses', () => {
  // State
  const courses = ref<Course[]>([]);
  const currentCourse = ref<Course | null>(null);
  const courseProgress = ref<Record<string, CourseProgress>>({});
  const isLoading = ref(false);
  const error = ref<string | null>(null);
  const pagination = ref({
    page: 1,
    limit: 12,
    total: 0,
    totalPages: 0,
  });

  // Filter and sort state
  const filters = ref<CourseFilters>({});
  const sortOptions = ref<CourseSortOptions>({
    field: 'createdAt',
    order: 'desc',
  });

  // Getters
  const featuredCourses = computed(() => 
    courses.value.filter(course => course.rating >= 4.5).slice(0, 4)
  );

  const popularCourses = computed(() => 
    courses.value
      .sort((a, b) => b.studentsCount - a.studentsCount)
      .slice(0, 8)
  );

  const freeCourses = computed(() => 
    courses.value.filter(course => course.isFree)
  );

  const coursesByCategory = computed(() => {
    const grouped: Record<string, Course[]> = {};
    courses.value.forEach(course => {
      if (!grouped[course.category]) {
        grouped[course.category] = [];
      }
      grouped[course.category].push(course);
    });
    return grouped;
  });

  // Actions
  const fetchCourses = async (resetPagination = false) => {
    isLoading.value = true;
    error.value = null;

    try {
      // Mock API call with delay
      await mockApiDelay(500);
      
      // Use mock courses data
      const mockResponse: PaginatedResponse<Course> = {
        data: mockCourses,
        pagination: {
          page: 1,
          limit: 12,
          total: mockCourses.length,
          totalPages: 1
        }
      };

      if (resetPagination) {
        courses.value = mockResponse.data;
        pagination.value.page = 1;
      } else {
        courses.value.push(...mockResponse.data);
      }

      pagination.value = {
        ...pagination.value,
        ...mockResponse.pagination,
      };
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка загрузки курсов';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const fetchCourse = async (id: string) => {
    isLoading.value = true;
    error.value = null;

    try {
      // Mock API call with delay
      await mockApiDelay(300);
      
      // Find course in mock data
      const course = mockCourses.find(c => c.id === id);
      if (!course) {
        throw new Error('Курс не найден');
      }
      
      currentCourse.value = course;
      
      // Set mock progress if exists
      if (mockCourseProgress[id]) {
        courseProgress.value[id] = mockCourseProgress[id];
      }
      
      return course;
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка загрузки курса';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const enrollInCourse = async (courseId: string) => {
    isLoading.value = true;
    error.value = null;

    try {
      await coursesApi.enrollInCourse(courseId);
      
      // Update the current course to reflect enrollment
      if (currentCourse.value?.id === courseId) {
        // In a real app, the backend would return updated course data
        // For now, we'll just refresh the course
        await fetchCourse(courseId);
      }
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка записи на курс';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const markLessonComplete = async (courseId: string, lessonId: string) => {
    try {
      await progressApi.markLessonComplete(courseId, lessonId);
      
      // Update local progress
      if (courseProgress.value[courseId]) {
        const progress = courseProgress.value[courseId];
        if (!progress.completedLessons.includes(lessonId)) {
          progress.completedLessons.push(lessonId);
          // Recalculate progress percentage
          if (currentCourse.value) {
            const totalLessons = currentCourse.value.modules.reduce(
              (total, module) => total + module.lessons.length, 
              0
            );
            progress.progressPercentage = (progress.completedLessons.length / totalLessons) * 100;
          }
        }
      }
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка отметки урока';
      throw err;
    }
  };

  const markModuleComplete = async (courseId: string, moduleId: string) => {
    try {
      await progressApi.markModuleComplete(courseId, moduleId);
      
      // Update local progress
      if (courseProgress.value[courseId]) {
        const progress = courseProgress.value[courseId];
        if (!progress.completedModules.includes(moduleId)) {
          progress.completedModules.push(moduleId);
        }
      }
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка отметки модуля';
      throw err;
    }
  };

  const updateFilters = (newFilters: Partial<CourseFilters>) => {
    filters.value = { ...filters.value, ...newFilters };
  };

  const updateSort = (newSort: Partial<CourseSortOptions>) => {
    sortOptions.value = { ...sortOptions.value, ...newSort };
  };

  const clearFilters = () => {
    filters.value = {};
  };

  const loadMoreCourses = async () => {
    if (pagination.value.page < pagination.value.totalPages) {
      pagination.value.page += 1;
      await fetchCourses(false);
    }
  };

  const searchCourses = async (query: string) => {
    isLoading.value = true;
    error.value = null;

    try {
      const results = await coursesApi.searchCourses(query);
      courses.value = results;
      // Reset pagination for search results
      pagination.value = {
        page: 1,
        limit: results.length,
        total: results.length,
        totalPages: 1,
      };
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка поиска';
      throw err;
    } finally {
      isLoading.value = false;
    }
  };

  const rateCourse = async (courseId: string, rating: number) => {
    try {
      await coursesApi.rateCourse(courseId, rating);
      
      // Update local course rating
      if (currentCourse.value?.id === courseId) {
        // In a real app, you'd get the updated rating from the backend
        // For now, we'll just refresh the course
        await fetchCourse(courseId);
      }
    } catch (err) {
      error.value = err instanceof Error ? err.message : 'Ошибка оценки курса';
      throw err;
    }
  };

  const clearError = () => {
    error.value = null;
  };

  const clearCurrentCourse = () => {
    currentCourse.value = null;
  };

  return {
    // State
    courses,
    currentCourse,
    courseProgress,
    isLoading,
    error,
    pagination,
    filters,
    sortOptions,

    // Getters
    featuredCourses,
    popularCourses,
    freeCourses,
    coursesByCategory,

    // Actions
    fetchCourses,
    fetchCourse,
    enrollInCourse,
    markLessonComplete,
    markModuleComplete,
    updateFilters,
    updateSort,
    clearFilters,
    loadMoreCourses,
    searchCourses,
    rateCourse,
    clearError,
    clearCurrentCourse,
  };
});
