import { createRouter, createWebHistory } from 'vue-router';
import { useAuthStore } from '@/stores/auth';

// Import pages
import Home from '@/pages/Home.vue';
import CoursesPage from '@/pages/CoursesPage.vue';
import CourseDetail from '@/components/coursePageComponent.vue';

// Auth pages (will be created)
const Login = () => import('@/pages/auth/Login.vue');
const Register = () => import('@/pages/auth/Register.vue');
const ForgotPassword = () => import('@/pages/auth/ForgotPassword.vue');

// Profile pages (will be created)
const Profile = () => import('@/pages/profile/Profile.vue');
const Dashboard = () => import('@/pages/profile/Dashboard.vue');
const MyCourses = () => import('@/pages/profile/MyCourses.vue');
const Settings = () => import('@/pages/profile/Settings.vue');

// Course learning pages (will be created)
const CourseLesson = () => import('@/pages/course/CourseLesson.vue');
const QuizPage = () => import('@/pages/course/QuizPage.vue');
const AIExercisePage = () => import('@/pages/course/AIExercisePage.vue');

// Admin pages (will be created)
const AdminDashboard = () => import('@/pages/admin/AdminDashboard.vue');
const AdminCourses = () => import('@/pages/admin/AdminCourses.vue');
const AdminUsers = () => import('@/pages/admin/AdminUsers.vue');

// Resume Analysis page
const ResumeAnalysis = () => import('@/pages/ResumeAnalysis.vue');

// Demo Lesson page
const DemoLesson = () => import('@/pages/DemoLesson.vue');

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { requiresAuth: false }
  },
  {
    path: '/courses',
    name: 'Courses',
    component: CoursesPage,
    meta: { requiresAuth: false }
  },
  {
    path: '/courses/:id',
    name: 'CourseDetail',
    component: CourseDetail,
    meta: { requiresAuth: false },
    props: true
  },
  {
    path: '/courses/:courseId/lessons/:lessonId',
    name: 'CourseLesson',
    component: CourseLesson,
    meta: { requiresAuth: true },
    props: true
  },
  {
    path: '/courses/:courseId/quiz/:quizId',
    name: 'Quiz',
    component: QuizPage,
    meta: { requiresAuth: true },
    props: true
  },
  {
    path: '/courses/:courseId/exercise/:exerciseId',
    name: 'AIExercise',
    component: AIExercisePage,
    meta: { requiresAuth: true },
    props: true
  },
  {
    path: '/resume-analysis',
    name: 'ResumeAnalysis',
    component: ResumeAnalysis,
    meta: { requiresAuth: false }
  },
  {
    path: '/demo-lesson',
    name: 'DemoLesson',
    component: DemoLesson,
    meta: { requiresAuth: false }
  },
  
  // Auth routes
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { requiresAuth: false, guestOnly: true }
  },
  {
    path: '/register',
    name: 'Register',
    component: Register,
    meta: { requiresAuth: false, guestOnly: true }
  },
  {
    path: '/forgot-password',
    name: 'ForgotPassword',
    component: ForgotPassword,
    meta: { requiresAuth: false, guestOnly: true }
  },
  
  // Profile routes
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        name: 'Dashboard',
        component: Dashboard
      },
      {
        path: 'courses',
        name: 'MyCourses',
        component: MyCourses
      },
      {
        path: 'settings',
        name: 'Settings',
        component: Settings
      }
    ]
  },
  
  // Admin routes
  {
    path: '/admin',
    name: 'Admin',
    meta: { requiresAuth: true, requiresAdmin: true },
    children: [
      {
        path: '',
        name: 'AdminDashboard',
        component: AdminDashboard
      },
      {
        path: 'courses',
        name: 'AdminCourses',
        component: AdminCourses
      },
      {
        path: 'users',
        name: 'AdminUsers',
        component: AdminUsers
      }
    ]
  },
  
  // Catch all 404
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('@/pages/NotFound.vue'),
    meta: { requiresAuth: false }
  }
];

const router = createRouter({
  history: createWebHistory('/test-vue-app/'),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    }
  }
});

// Navigation guards
router.beforeEach(async (to, from, next) => {
  const authStore = useAuthStore();
  
  // Initialize auth store if needed
  if (!authStore.user && localStorage.getItem('auth_token')) {
    await authStore.initializeAuth();
  }
  
  const requiresAuth = to.meta.requiresAuth;
  const guestOnly = to.meta.guestOnly;
  const requiresAdmin = to.meta.requiresAdmin;
  const isAuthenticated = authStore.isAuthenticated;
  const isAdmin = authStore.isAdmin;
  
  // Check if route requires authentication
  if (requiresAuth && !isAuthenticated) {
    next({
      name: 'Login',
      query: { redirect: to.fullPath }
    });
    return;
  }
  
  // Check if route is for guests only (login, register pages)
  if (guestOnly && isAuthenticated) {
    next({ name: 'Dashboard' });
    return;
  }
  
  // Check if route requires admin privileges
  if (requiresAdmin && !isAdmin) {
    next({ name: 'Home' });
    return;
  }
  
  next();
});

export default router;
