<template>
  <div class="bg-white shadow-md rounded-2xl p-4 flex flex-col h-full hover:shadow-lg transition-shadow cursor-pointer">
    <div class="flex items-center gap-2 mb-2">
      <span :class="`text-xs font-semibold px-2 py-1 ${getCategoryColors(course.category).bg} ${getCategoryColors(course.category).text} rounded-full`">
        {{ getCategoryLabel(course.category) }}
      </span>
      <span class="text-sm text-gray-500">{{ formatDuration(course.duration) }}</span>
      <span class="text-sm text-gray-500">•</span>
      <span class="text-sm text-gray-500">{{ getLevelLabel(course.level) }}</span>
    </div>
    <h3 class="text-lg font-semibold mb-2">{{ course.title }}</h3>
    <p class="text-sm text-gray-600 flex-grow">{{ course.shortDescription }}</p>
    <div class="mt-4 flex justify-between items-center text-sm text-gray-700">
      <span>{{ course.instructor.name }}</span>
      <span class="flex items-center gap-1 text-yellow-500 font-semibold">
        <svg class="w-4 h-4 fill-current" viewBox="0 0 24 24">
          <path d="M12 .587l3.668 7.571 8.332 1.151-6.064 5.982 1.51 8.272L12 18.897l-7.446 4.666 1.51-8.272L0 9.309l8.332-1.151z" />
        </svg>
        {{ course.rating }}
      </span>
    </div>
    <div class="mt-3 flex justify-between items-center">
      <span class="text-sm text-gray-500">{{ course.studentsCount }} студентов</span>
      <span class="text-lg font-bold text-blue-600">
        {{ course.isFree ? 'Бесплатно' : `${course.price} ₽` }}
      </span>
    </div>
  </div>
</template>

<script setup lang="ts">
import type { Course } from '@/types';

interface Props {
  course: Course;
}

const props = defineProps<Props>();

const getCategoryLabel = (category: string): string => {
  const labels: Record<string, string> = {
    'frontend': 'КЛИЕНТСКАЯ ЧАСТЬ',
    'backend': 'СЕРВЕРНАЯ ЧАСТЬ',
    'ai_ml': 'AI/ML',
    'devops': 'DEVOPS',
    'data_science': 'НАУКА О ДАННЫХ',
    'design': 'DESIGN',
    'mobile': 'MOBILE',
    'other': 'ДРУГОЕ'
  };
  return labels[category] || 'ПРОГРАММИРОВАНИЕ';
};

const getCategoryColors = (category: string): { bg: string; text: string } => {
  const colors: Record<string, { bg: string; text: string }> = {
    'frontend': { bg: 'bg-green-100', text: 'text-green-800' }, 
    'backend': { bg: 'bg-blue-100', text: 'text-blue-800' },
    'ai_ml': { bg: 'bg-purple-100', text: 'text-purple-800' },
    'devops': { bg: 'bg-orange-100', text: 'text-orange-800' },
    'data_science': { bg: 'bg-cyan-100', text: 'text-cyan-800' },
    'design': { bg: 'bg-pink-100', text: 'text-pink-800' },
    'mobile': { bg: 'bg-indigo-100', text: 'text-indigo-800' },
    'other': { bg: 'bg-gray-100', text: 'text-gray-800' }
  };
  return colors[category] || { bg: 'bg-blue-100', text: 'text-blue-800' };
};

const getLevelLabel = (level: string): string => {
  const labels: Record<string, string> = {
    'beginner': 'Начальный',
    'intermediate': 'Средний',
    'advanced': 'Продвинутый'
  };
  return labels[level] || 'Начальный';
};

const formatDuration = (minutes: number): string => {
  const hours = Math.floor(minutes / 60);
  const mins = minutes % 60;
  
  if (hours > 0) {
    return mins > 0 ? `${hours}ч ${mins}м` : `${hours}ч`;
  }
  return `${mins}м`;
};
</script>

