<template>
  <header class="bg-white border-b border-gray-200 sticky top-0 z-50">
    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div class="flex justify-between items-center h-16">
        <!-- Logo and Navigation -->
        <div class="flex items-center space-x-8">
          <router-link to="/" class="flex-shrink-0">
            <div class="flex items-center">
              <span class="text-2xl font-bold text-blue-600">🧠</span>
              <span class="ml-2 text-xl font-bold text-gray-900">AILearn</span>
            </div>
          </router-link>
          
          <nav class="hidden md:flex space-x-8">
            <router-link
              to="/"
              class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium"
              active-class="text-blue-600"
            >
              Главная
            </router-link>
            <router-link
              to="/courses"
              class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium"
              active-class="text-blue-600"
            >
              Курсы
            </router-link>
            <router-link
              to="/resume-analysis"
              class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium"
              active-class="text-blue-600"
            >
              🤖 Анализ резюме
            </router-link>
            <a href="#" class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium">
              Для бизнеса
            </a>
            <a href="#" class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium">
              Цены
            </a>
          </nav>
        </div>

        <!-- User Menu -->
        <div class="flex items-center space-x-4">
          <!-- Search (для будущего функционала) -->
          <div class="hidden lg:block">
            <div class="relative">
              <input
                type="text"
                placeholder="Поиск курсов..."
                class="w-64 pl-3 pr-10 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500 text-sm"
                @keyup.enter="handleSearch"
                v-model="searchQuery"
              />
              <div class="absolute inset-y-0 right-0 pr-3 flex items-center">
                <span class="text-gray-400">🔍</span>
              </div>
            </div>
          </div>

          <!-- Authentication Menu -->
          <div v-if="!authStore.isAuthenticated" class="flex items-center space-x-3">
            <router-link
              to="/login"
              class="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium"
            >
              Войти
            </router-link>
            <router-link
              to="/register"
              class="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg text-sm font-medium"
            >
              Регистрация
            </router-link>
          </div>

          <!-- User Dropdown -->
          <div v-else class="relative">
            <button
              @click="showUserMenu = !showUserMenu"
              class="flex items-center space-x-2 text-gray-700 hover:text-gray-900"
            >
              <img
                :src="authStore.user?.avatar || defaultAvatar"
                :alt="authStore.user?.fullName"
                class="h-8 w-8 rounded-full object-cover"
              />
              <span class="hidden md:block text-sm font-medium">{{ authStore.user?.fullName }}</span>
              <span class="text-gray-400">▼</span>
            </button>

            <!-- Dropdown Menu -->
            <div
              v-if="showUserMenu"
              class="absolute right-0 mt-2 w-56 bg-white border border-gray-200 rounded-lg shadow-lg z-50"
              @click.away="showUserMenu = false"
            >
              <div class="p-3 border-b border-gray-100">
                <p class="text-sm font-medium text-gray-900">{{ authStore.user?.fullName }}</p>
                <p class="text-xs text-gray-500">{{ authStore.user?.email }}</p>
              </div>
              
              <div class="py-1">
                <router-link
                  to="/profile"
                  class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  @click="showUserMenu = false"
                >
                  📊 Панель управления
                </router-link>
                <router-link
                  to="/profile/courses"
                  class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  @click="showUserMenu = false"
                >
                  📚 Мои курсы
                </router-link>
                <router-link
                  to="/profile/settings"
                  class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  @click="showUserMenu = false"
                >
                  ⚙️ Настройки
                </router-link>
              </div>
              
              <div class="border-t border-gray-100 py-1">
                <button
                  @click="handleLogout"
                  class="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-gray-100"
                >
                  🚪 Выйти
                </button>
              </div>
            </div>
          </div>

          <!-- Mobile Menu Button -->
          <button
            @click="showMobileMenu = !showMobileMenu"
            class="md:hidden p-2 rounded-lg text-gray-400 hover:text-gray-600 hover:bg-gray-100"
          >
            <span class="sr-only">Открыть меню</span>
            <span class="text-lg">☰</span>
          </button>
        </div>
      </div>

      <!-- Mobile Menu -->
      <div v-if="showMobileMenu" class="md:hidden border-t border-gray-200 py-4">
        <nav class="space-y-2">
          <router-link
            to="/"
            class="block px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg"
            @click="showMobileMenu = false"
          >
            Главная
          </router-link>
          <router-link
            to="/courses"
            class="block px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg"
            @click="showMobileMenu = false"
          >
            Курсы
          </router-link>
          <router-link
            to="/resume-analysis"
            class="block px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg"
            @click="showMobileMenu = false"
          >
            🤖 Анализ резюме
          </router-link>
          
          <div v-if="!authStore.isAuthenticated" class="pt-4 border-t border-gray-200">
            <router-link
              to="/login"
              class="block px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg"
              @click="showMobileMenu = false"
            >
              Войти
            </router-link>
            <router-link
              to="/register"
              class="block px-3 py-2 text-blue-600 hover:bg-gray-100 rounded-lg font-medium"
              @click="showMobileMenu = false"
            >
              Регистрация
            </router-link>
          </div>
        </nav>
      </div>
    </div>
  </header>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { useAuthStore } from '@/stores/auth';

const router = useRouter();
const authStore = useAuthStore();

const showUserMenu = ref(false);
const showMobileMenu = ref(false);
const searchQuery = ref('');

const defaultAvatar = computed(() => 
  'https://ui-avatars.com/api/?name=' + 
  encodeURIComponent(authStore.user?.fullName || 'User') + 
  '&background=3B82F6&color=fff'
);

const handleSearch = () => {
  if (searchQuery.value.trim()) {
    router.push(`/courses?search=${encodeURIComponent(searchQuery.value.trim())}`);
    searchQuery.value = '';
  }
};

const handleLogout = async () => {
  await authStore.logout();
  showUserMenu.value = false;
  router.push('/');
};

// Close menus when clicking outside
document.addEventListener('click', (e) => {
  const target = e.target as HTMLElement;
  if (!target.closest('.relative')) {
    showUserMenu.value = false;
  }
});
</script>